/* -------------------- header -------------------- */
.header {
    overflow: hidden;
    background-color: #1A002E;
    padding: 20px 10px;
    display: flex;
}*{
    margin: 0%;
  }

/* SOB */
.header a{
    float: left;
    text-align: center;
    padding: 12px;
    text-decoration: none;
    font-size: 24px;
    line-height: 25px;
    border-radius: 4px;
    color: white;
    font-weight: normal;
    font-family: Georgia, 'Times New Roman', Times, serif;
  }

.header a {
    float: none;
    display: block;
    text-align: left;
}

/* right side - bpsu */
.header-right {
    float: right;
    position: absolute;
    top: 0;
    right: 0;
    padding: 25px 20px;
}

.logo {
    position:absolute;
    top: 5px;
    right: 265px;
}

/* -------------------- navbar -------------------- */
.navbar {
    overflow: hidden;
    background-color: #EBEBEB;
    display: flex;
}*{
    margin: 0%;
  }

/* navbar buttons */  
.navbar a {
    float: left;
    color: black;
    text-align: center;
    padding-left: 5px;
    padding-right: 10px;
    text-decoration: none;
    font-size: 16px; 
    line-height: 25px;
    border-radius: 4px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: normal;
}

/* Change the background color on mouse-over */
.navbar a:hover {
  background-color: #1A002E;
  color: white;
}

/* -------------------- TYPOGRAPHY -------------------- */
h4 {
    color: white;
    font-weight: lighter;
    font-family: Georgia, 'Times New Roman', Times, serif;
}

/* -------------------- media queries for responsiveness -------------------- */
@media screen and (max-width: 500px) {
    .header {
      float: none;
      display: block;
      text-align: left;
    }
    .header-right {
        float: none;
    }
    .logo {
      float: none;
    }
  }

/* ------------------------------------------------------------ bulletin page -------------------- */
/* announcement display */
.display-announcement {
    border-radius: 13px;
    background: #E7E9EB;
    padding: 20px; 
    width: 1000px;  
    margin: auto;
    margin-top: 25px;
    border: 10px solid #1A002E;
}

/* ------------------------------------------------------------ organizations page -------------------- */
html {
    scroll-behavior: smooth;
}
.orgs-page {
    left: 0px;
    top: 89px;
    filter: brightness(50%)
}
.txt1 {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bolder;
    font-size: x-large;
    color: white;
    text-shadow: 2px 2px 5px black;
}

/* Three image containers (use 25% for four, and 50% for two, etc) */
.org-row-1 {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    padding: 30px;
    transition: .5s ease;
    backface-visibility: hidden;
}

.org-row-2 {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    padding: 30px;
    transition: .5s ease;
    backface-visibility: hidden;
    padding-top: 70px;
}

.org-row-3 {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    padding: 30px;
    transition: .5s ease;
    backface-visibility: hidden;
    padding-top: 70px;
}

.org-row-4 {
    margin-top: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100px;
    padding: 30px;
    transition: .5s ease;
    backface-visibility: hidden;
    padding-top: 70px;
}

.org-logos {
    opacity: 1;
    display: block;
    width: 100%;
    height: auto;
    transition: .5s ease;
    backface-visibility: hidden;
    padding: 30px;
  }


.org-column:hover .org-logos {
  -ms-transform: scale(1.5); /* IE 9 */
  -webkit-transform: scale(1.5); /* Safari 3-8 */
  transform: scale(1.5); 
}

/* ------------------------------------------------------------ publish announcement page -------------------- */

/* -------------------- Input announcement -------------------- */
.input-announcement {
  border-radius: 13px;
  background: #666666;
  padding: 20px; 
  width: 1000px;
  margin: auto;
  margin-top: 25px;
}

/* announcement text area */
.announcement-txtarea {
    background-color: #dddddd;
    color: black;
    padding: 1em;
    border-radius: 10px;
    border: 2px solid transparent;
    outline: none;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    font-size: 16px;
    line-height: 1.4;
    transition: 0.2s;
}

.announcement-txtarea:hover {
    cursor: pointer;
    background-color: #eeeeee;
}

.announcement-txtarea:focus {
    cursor: text;
    color: #333333;
    background-color: white;
    border-color: #1A002E;
}

/* publish announcement button */
.publish-btn {
    background: #395596;
    border-radius: 10px;
    padding: 8px 15px;
    font-size: medium;
    margin-top: 10px;
    margin-left: 1175px;
    border: 2px solid transparent;
    color: white;
}

.publish-btn:hover {
    background-color: #1A002E;
    color: white;
}

/* ------------------------------------------------------------ submit event proposal page -------------------- */
.submit-container {
    border-radius: 13px;
    background: #666666;
    padding: 20px; 
    width: 700px;
    margin: auto;
    margin-top: 25px;
}

.submit-inner {
    background-color: #EBEBEB;
    color: black;
    padding: 1em;
    border-radius: 10px;
    border: 2px solid transparent;
    outline: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: small;
    line-height: 1.4;
    transition: 0.2s;
}

/* event-details */
.event-details {
  font-family: Arial, Helvetica, sans-serif;
  color: black;
  font-size: medium;
}

.event-input-details {
  background-color: #dddddd;
  padding: 5px;
  outline: #1A002E;
  margin-right: 20px;
  border-radius: 10px;
  border: 2px solid transparent;
  font-family: Arial, Helvetica, sans-serif;
  font-size: medium;
  transition: 0.2s;
  margin-bottom: 20px;
  display: flex;
}


.event-input-details:hover {
cursor: pointer;
background-color: #eeeeee;
}

.event-input-details:focus {
cursor: text;
color: #333333;
background-color: white;
border-color: #1A002E;
}

.submit-event-btn {
  background: #395596;
  border-radius: 10px;
  padding: 8px 15px;
  font-size: medium;
  margin-top: 10px;
  margin-left: 1030px;
  border: 2px solid transparent;
  color: white;
}

.submit-event-btn:hover {
  background-color: #1A002E;
  color: white;
}

